@keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

.lds-css {
    margin: 0 auto;
}

.lds-double-ring {
    position: relative;
}
.lds-double-ring div {
    position: absolute;
    width: 320px;
    height: 320px;
    top: 40px;
    left: 40px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #3d9090 transparent #3d9090 transparent;
    -webkit-animation: lds-double-ring 1.5s linear infinite;
    animation: lds-double-ring 1.5s linear infinite;
}
.lds-double-ring div:nth-child(2) {
    width: 280px;
    height: 280px;
    top: 60px;
    left: 60px;
    border-color: transparent #3d5454 transparent #3d5454;
    -webkit-animation: lds-double-ring_reverse 1.5s linear infinite;
    animation: lds-double-ring_reverse 1.5s linear infinite;
}
.lds-double-ring {
    width: 77px !important;
    height: 77px !important;
    -webkit-transform: translate(-38.5px, -38.5px) scale(0.385)
        translate(38.5px, 38.5px);
    transform: translate(-38.5px, -38.5px) scale(0.385)
        translate(38.5px, 38.5px);
}
